@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
* {
	box-sizing: border-box;
	font-family: 'Roboto', sans-serif;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
body {
	background-color: #D4D7D9;
}

.container {
  max-width: 1140px;
  margin: 0 auto;
  display: block;
  padding: 0 20px;
  position: relative;
}

.header {
  background: #3C4151;
  padding: 25px 0 35px;
  height: 290px;
}

.header .container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.header a {
  display: inline-block;
  max-width: 165px;
}

.header p {
  color: #ffffff;
  text-align: center;
  margin-top: 25px;
}

.header .header-link {
  position: absolute;
  top: 0;
  left: 20px;
}
.card {
  background: #FFFFFF;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 6px;
  padding: 20px;
  position: relative;
  width: 100%;
  cursor: pointer;
  transition: 0.2s all ease-in-out;
}

.card img {
  max-width: 130px;
  max-height: 130px;
  display: block;
  margin: 0 auto;
}

.card h4 {
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  text-align: center;
  color: #3C4151;
  margin-bottom: 5px;
}

.card p {
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  text-align: center;
  color: #6D6C6C;
}

.card .check {
  border: 1px solid #6D6C6C;
  border-radius: 100%;
  height: 20px;
  width: 20px;
  display: block;
  position: absolute;
  top: 10px;
  right: 10px;
}

.card.disabled {
  opacity: 0.5;
  cursor: auto;
  cursor: initial;
}

.card.active {
  border: 1px solid #67A712;
  transform: scale(1.05);
  opacity: 1;
  cursor: pointer;
}

.card.active .check{
  background-color: #67A712;
  border: 1px solid #67A712;
  background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAwAAAAKCAYAAACALL/6AAAACXBIWXMAAAsTAAALEwEAmpwYAAAAAXNSR0IArs4c6QAAAARnQU1BAACxjwv8YQUAAABiSURBVHgBjVBbEcAwCEPCJCBhUiZhEuagc1gJlZJxt77pg9zlh0sggcgIALeQrWKHH94iPlHw5OFEzMIQxW8aXumc8OgMXkXptmRTlTuosgOTq3IzGfK2JRcf0SV3iKbp5g9y6LSNBP9rcgAAAABJRU5ErkJggg==);
  background-repeat: no-repeat;
  background-position: center;
}
.sub-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 50px 0;
  border-bottom: 1px solid #6D6C6C;
}

.sub-header .selected-squad  p{
  font-style: normal;
  font-weight: bold;
  font-size: 25px;
  color: #3C4151;
  margin-bottom: 8px;
}

.sub-header .selected-squad  span{
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  color: #6D6C6C;
}

.sub-header h2 {
  font-style: normal;
  font-weight: bold;
  font-size: 35px;
  color: #3C4151;
}

.sub-header button {
  background: #67A712;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 6px;
  color: #ffffff;
  text-transform: uppercase;
  padding: 5px 15px;
  width: 180px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 0;
  cursor: pointer;
}

.sub-header button:disabled {
  cursor: default;
  background-color: #a7a7a7;
  box-shadow: none;
}

.sub-header button img {
  max-width: 20px;
  margin-right: 10px;
}

.card-list {
  display: grid;
  grid-template-rows: 240px;
  grid-gap: 25px;
  gap: 25px;
  grid-template-columns: repeat(4, 1fr);
  margin: 50px 0;
}

@media (max-width: 991px) {
  .card-list {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 767px) {
  .sub-header {
    flex-wrap: wrap;
    justify-content: center;
    text-align: center;
    padding: 20px;
  }

  .sub-header .selected-squad, .sub-header h2, .sub-header button {
    width: 100%;
    margin: 10px 0;
  }

  .card-list {
    grid-template-columns: repeat(1, 1fr);
    margin: 30px;
  }
}

.title {
  font-style: normal;
  font-weight: bold;
  font-size: 35px;
  text-align: center;
  color: #3C4151;
  margin-top: 30px;
}

.result {
  max-width: 665px;
  margin: 50px auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.result-item {
  width: 100%;
  max-width: 250px;
}

.result-versus {
  max-width: 26px;
}

.result-item .result-trophy {
  margin-top: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.result-item .result-trophy p {
  font-style: normal;
  font-weight: bold;
  font-size: 80px;
  text-align: center;
  color: #3C4151;
  margin-right: 10px;
}

@media (max-width: 767px) {
  .result {
    flex-wrap: wrap;
    justify-content: center;
  }

  .result .result-item{
    max-width: none;
    max-width: initial;
  }

  .result .result-versus {
    margin: 30px 0;
  } 
}
